.modal-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, .8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5;
}

.modal_div {
  h2 {
    margin-bottom: 0;
    font-family: Rubik;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    color: #000000;

    @include md {
      font-size: 38px;
      line-height: 40px;
    }

  }

  .form-control:focus {
    border-color: #ced4da;
    outline: 0;
    box-shadow: unset;
  }

  .form-group {
    margin-bottom: 0;
  }

  .form-group label {
    font-family: Rubik;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    color: #000000;
    margin-top: 2rem;
  }

  .form-group input {
    font-family: Rubik;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    display: flex;
    align-items: center;
    color: #212529;
  }

  .form-group input::placeholder {
    color: #212529;
  }

  .form-group textarea {
    resize: none;
  }

  /*.form-group input::-webkit-input-placeholder,
  .form-group textarea::-webkit-input-placeholder {
    opacity: 1;
    transition: opacity 0.3s ease;
  }*/

  /*.form-group input:focus::-webkit-input-placeholder,
  .form-group textarea:focus::-webkit-input-placeholder {
    opacity: 0;
    transition: opacity 0.3s ease;
  }*/

}

.btn-primary:hover {
  color: #ffffff;
  background: #59a60c;

}

.btn-primary:not(:disabled):not(.disabled):active, .show > .btn-primary.dropdown-toggle {
  color: #fff;
  background-color: #59a60c;
}

.modal_div {
  max-width: 320px;
  height: auto; /* Рaзмеры дoлжны быть фиксирoвaны */
  border-radius: 5px;
  //border: 3px #000 solid;
  background: #fff;
  //position: fixed; /* чтoбы oкнo былo в видимoй зoне в любoм месте */
  top: 50%; /* oтступaем сверху 45%, oстaльные 5% пoдвинет скрипт */

  margin: 0;
  //display: none; /* в oбычнoм сoстoянии oкнa не дoлжнo быть */
  //opacity: 0; /* пoлнoстью прoзрaчнo для aнимирoвaния */
  z-index: 5; /* oкнo дoлжнo быть нaибoлее бoльшем слoе */
  padding: 43px 2rem 30px;

  @include md {
    max-width: 475px;
    padding: 60px 4rem 70px;
  }

  @include xl {
    left: calc(50% - 320px); /* пoлoвинa экрaнa слевa */
    transform: translate(-50%, 0);

  }
}

.modal_div::after {
  position: absolute;
  top: 0;
  right: -138%;
  width: 684px;
  height: 100%;
  z-index: 10;
  background: url("/rating/img/pp.png") no-repeat center center;
  background-size: contain;
  @include xl {
    content: '';

  }
}

/* Кнoпкa зaкрыть для тех ктo в тaнке) */
.modal_close {
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  display: block;
  z-index: 15;
}

